@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400;700&display=swap);
:root {
  --primary-color: #171a20cc;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Roboto Condensed", "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

.btn {
  margin: 0.5rem;
  padding: 0.7rem 1.5rem;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: uppercase;
  border-radius: 25px;
  border: none;
  background: rgba(255, 255, 255, 0.1);
  width: 90%;
  color: #f6f6f6;
  cursor: pointer;
}

.btn-dark {
  background: rgba(0, 0, 0, 0.5);
}

.btn:hover {
  background: rgba(255, 255, 255, 0.4);
}
.btn-dark:hover {
  background: rgba(0, 0, 0, 0.8);
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.contact {
    width: 100%;
    height: 100vh;
    background-color: blue;
    /* background: url('../../assets/contact-bg.jpg') no-repeat center center/cover; */
}

.contact .container {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.contact .top {
    margin-top: 7rem;
    color: #f6f6f6;
}
.contact .bottom { 
    display: flex; 
    justify-content: center; 
    width: 100%; 
    max-width: 600px; 
    margin-bottom: 4rem;
    -webkit-transform: translateX(2.0%);
            transform: translateX(2.0%);

    
}



.contact form {
    max-width: 600px;
    width: 100%;
}

.contact form div {
    display: flex;
    flex-direction: column;
    margin: .5rem;
    
}

.contact form label {
    margin: 1rem 0 .2rem .7rem;
    color: #eee;
}


.contact form input, textarea {
    margin: .5rem;
    padding: .7rem 1.5rem;
    background: rgba(255, 255, 255, .3);
    border: none;
    border-radius: 25px;
    color: #f6f6f6;
    font-size: 1rem;
    outline: none;
    font-family: "Roboto Condensed", sans-serif;
}

.contact form input:hover, textarea:hover {
    box-shadow: 0 0 4px 0;
}

.contact form input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
    color: #333;
}

.contact form input::placeholder, textarea::placeholder {
    color: #333;
}






